import React, { ReactElement, useCallback, useEffect, useState, useContext} from 'react';
import { Redirect, Link } from 'react-router-dom';
import { PairingsProps } from '../tournament/Pairings';
import styles from '../css/pairings.module.css';
import { GET } from '../utils/requests';
import { TournamentSettings } from '../resources/settings';
import { FaAngleDoubleRight, FaPlusCircle } from 'react-icons/fa';
import { AiOutlineTable } from 'react-icons/ai';
import { IoIosStats } from 'react-icons/io';
import { watch } from '../assets/icons';
import { LoginContext } from '../App';
import { capitalise } from '../utils/prototype';

export function UserSwitcher(props: {
	section: string
	loaded: boolean
	classNames?: {[key: string]: string}
	sections: {
		[key: string]: {
			name?: string
			link: string
			icon: JSX.Element
			hide?: boolean,
			index?: number,
			className?: string
		}
	}
}): ReactElement {

	let selected = Object.keys(props.sections).find(k => props.section === k) || Object.keys(props.sections)[0];

	return <>
		<div className={[styles.modeSwitcher, props.classNames?.switcher].join(' ')}>
			{Object.entries(props.sections).sort((a, b) => (a[1].index ?? Infinity) - (b[1].index ?? Infinity)).map(([k, s], i) => {
				if (s.hide) return null;
				if (!props.loaded) {
					return <div className={['header-button'].join(' ')} key={['header-button', i].join(' ')}>
						{'\u200b'}
					</div>;
				} else {
					return <Link to={s.link} key={['header-button', i].join('.')} className={['header-button', s.className, selected === k ? 'selected' : ''].join(' ')}>
						<div>{s.icon}{s.name || capitalise(k)}</div>
					</Link>;
				}
			})}
		</div>
	</>;
}

export function ModeSwitcher(props: {
	id: string,
	section: 'pairings' | 'standings' | 'stats' | 'tv'
	settings: TournamentSettings
	round: number
	loaded: {[key: string]: boolean}
}): ReactElement {

	const loggedIn = useContext(LoginContext);
	const [shouldRedirect, redirectTo] = useState('');

	const handleJoin = useCallback(() => {
		if (!loggedIn) return redirectTo('/login');
		return null;
	}, [loggedIn, redirectTo]);

	if (shouldRedirect) return <Redirect to={shouldRedirect} />;

	return <>
		<UserSwitcher {...props}	loaded={props.loaded.all} sections={{
			pairings: {
				name: 'Pairings',
				link: `/t/${props.id}/pairings`,
				icon: <FaAngleDoubleRight />
			},
			standings: {
				name: 'Standings',
				link: `/t/${props.id}/standings`,
				icon: <AiOutlineTable />,
				index: props.settings.pairingSystem === 'knockout' ? 0 : undefined
			},
			stats: {
				name: 'Pairings',
				link: `/t/${props.id}/stats`,
				icon: <IoIosStats />
			},
			tv: {
				name: 'Pairings',
				link: `/t/${props.id}/tv`,
				icon: <FaAngleDoubleRight />,
				className: 'stream-link',
				hide: !props.settings.streamURL
			},
		}} />
		{props.settings.allowRegistration && props.settings.allowRegistration !== 'closed' ? <div className={['header-button', styles.join].join(' ')} onClick={handleJoin}>
			<div><FaPlusCircle />Join this tournament</div>
		</div> : null}
	</>;
}

export function RoundSwitcher(props: PairingsProps & {
	section: 'pairings' | 'standings' | 'stats' | 'tv',
	round: number
	loaded: {[key: string]: boolean}
}): ReactElement {

	const [roundTimes, setRoundTimes] = useState([]);
	const fetchRoundTimes = useCallback(() => GET({ url: `tournament/${props.id}/fetchRounds` })
		.then(setRoundTimes), [props.id, setRoundTimes]);
	useEffect(() => {
		fetchRoundTimes();
	}, [fetchRoundTimes]);

	const showResults = props.loaded.status && props.section === 'pairings' && props.round && !props.status.active;
	const showIndividual = props.loaded.status && props.section === 'standings' && props.settings.competitors === 'team';

	let rounds = [];
	let totalRounds = props.settings.totalRounds === Infinity || props.settings.totalRounds === 'Infinity' ? 1 : props.settings.totalRounds;
	for (let i = 1; i < totalRounds + 1; i++) {
		let date = roundTimes[i] ? new Date(roundTimes[i]).toString() : '';
		rounds.push(
			<Link
				className={['header-button', styles.pairingRound, props.round === i ? styles.roundSelected : '', i > props.status.round ? styles.nullRound : ''].join(' ')}
				to={`/t/${props.id}/${props.section}/${i}`}
				key={['round', i].join('.')}
			>
			<div className='full-text'>
				{props.loaded.all ?
					<>
						<h4>{!showResults ? 'Round ' : ''}{i}</h4>
						{roundTimes[i] ? 
							<h5>{date.substring(0, 11) + date.substring(16, 21)}</h5> :
							null
						}
					</>
				: '\u200b'}
				</div>
			</Link>
		);
	}
	if (showResults) rounds.push(
		<Link
			className={['header-button', styles.pairingRound, props.round === totalRounds + 1 ? styles.roundSelected : ''].join(' ')}
			to={`/t/${props.id}/${props.section}/results`}
		>
			<div className='full-text'>
				<h4>Results</h4>
				{roundTimes[totalRounds + 1] ? <h5>{new Date(roundTimes[totalRounds + 1]).toString().substring(0, 11) + new Date(roundTimes[totalRounds + 1]).toString().substring(16, 21)}</h5> : null}
			</div>				
		</Link>
	)

	if (showIndividual) rounds.push(
		<Link
			className={['header-button', styles.pairingRound, props.round === totalRounds + 1 ? styles.roundSelected : ''].join(' ')}
			to={['', 't', props.id, props.section, 'individual'].join('/')}
		>
			<div className='full-text'>
				<h4>Individual Scores</h4>
			</div>
		</Link>
	)

	return (
		<div className={['container', 'bodyContainer'].join(' ')}>
			{rounds.length ?
				<div className='button-list'>
					{rounds}
				</div> :
			null}
		</div>
	);
}